.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  &__top {
    margin-bottom: 24px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    padding-inline: 20px;

    @include onTablet {
      padding-inline: 72px;
    }

    @include onDesktop {
      padding-inline: 120px;
    }
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: #1d1d1d;
    text-decoration: none;
  }

  &__book-ride {
    display: block;
    width: fit-content;
    padding-bottom: 8px;
    border-bottom: 1px solid #1d1d1d;
    border-radius: 2px;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2;
    color: #1d1d1d;
    text-decoration: none;
  }
}
